.damage-chart-list{
    display: flex;
    list-style-type: none;
    padding-left: 0px;
    column-gap: 20px;
}

.damage-hero-data-container{
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding-left: 0px;
    row-gap: 20px;
}

.damage-hero-title{
    text-align:center;
}

.damage-hero-img-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 120px;
    cursor: pointer;
    position: relative;
}

.damage-hero-stats{
    position: absolute;
    bottom: 0px;
    right: 0px;
    font-weight: 600;
    font-size: 14px;
}